import axios from "axios"

// Inicio da rota base
export const api = axios.create({
    baseURL: "http://localhost:5000/api",
})
// Fim da rota base

// Inicio da chamada dos consultores via api
export const listarConsultor = async () => {
    return api.get("/consultor/listar");
};

export const faturasConsultor = async (consultor, mes_inicio, ano_inicio, mes_fim, ano_fim) => {
    return api.post("/consultor/faturas", {
        "consultor": consultor, "mes_inicio": mes_inicio,
        "ano_inicio": ano_inicio,
        "mes_fim": mes_fim,
        "ano_fim": ano_fim
    });
} 
// Fim da chamada dos consultores via api

// Inicio da chamada dos clientes via api
export const listarCliente = async () => {
    return api.get("/cliente/listar");
};

export const faturasCliente = async (cliente, mes_inicio, ano_inicio, mes_fim, ano_fim) => {
    return api.post("/cliente/faturas", {
        "cliente": cliente, "mes_inicio": mes_inicio,
        "ano_inicio": ano_inicio,
        "mes_fim": mes_fim,
        "ano_fim": ano_fim
    });
} 
// Fim da chamada dos clientes via api