import React, { useEffect, useState } from 'react';
import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Select from "react-select";
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import { Chart } from "react-google-charts";
import _ from "lodash";


import {
    MonthPickerDropdown
} from 'react-month-picker-dropdown'
import 'react-month-picker-dropdown/dist/index.css'

import { listarConsultor, faturasConsultor } from "../../services/api"

const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
}));

export const options = {
    title: "Performance Comercial",
    hAxis: { title: "Meses" },
    seriesType: "bars",
    series: { 2: { type: "line" } },
};

export const optionsPizza = {
    title: "Participacao na receira",
    is3D: true,
};

export default function Consultor() {
    const [consultores, setConsultores] = useState([]);
    const [consultoresSelecionados, setConsultoresSelecionados] = useState([]);
    const [loading, setLoading] = useState(true);
    const [faturasAgrupadas, setFaturasAgrupadas] = useState([]);
    const [pizza, setPizza] = useState([]);
    const [grafico, setGrafico] = useState([])
    const [mesInicio, setMesInicio] = useState([]);
    const [mesFim, setMesFim] = useState([]);
    const [anoInicio, setAnoInicio] = useState([]);
    const [anoFim, setAnoFim] = useState([]);
    const [showRelatorio, setShowRelatorio] = useState(false);
    const [showGrafico, setShowGrafico] = useState(false);
    const [showPizza, setShowPizza] = useState(false)

    const relatorioConsultores = [];
    const relatorioGraficoBarras = [];
    const relatorioPizza= [];


    useEffect(() => {
        (async () => {
            const response = await listarConsultor();
            setConsultores(response.data);
            setLoading(false);
        })()
    }, [])

    if (loading) {
        return <div className='loading'> carregando dados...</div>
    }

    const handleAMonthChange = (event) => {
        setMesInicio(event.startMonth);
        setMesFim(event.endMonth);
        setAnoInicio(event.startYear);
        setAnoFim(event.endYear);
    }

    const groupByConsultor = (list) => {
        const map = new Map();
        list.forEach((item) => {
            const key = item.no_usuario;
            const collection = map.get(key);
            if (!collection) {
                map.set(key, [item]);
            } else {
                collection.push(item);
            }
        });
        return map;
    }

    const handleSelect = (consultor) => {
        let newArray = consultor.map(value => value.value)
        setConsultoresSelecionados(newArray)
        console.log(newArray)
    }

    const handRelatorio = async () => {
        const response = await faturasConsultor(consultoresSelecionados, mesInicio, anoInicio, mesFim, anoFim);
        const resultadoAgrupados = groupByConsultor(response.data);
        setFaturasAgrupadas(resultadoAgrupados);
        setShowRelatorio(true);
        setShowPizza(false)
        setShowGrafico(false)
    };

    const handGrafico = async () => {
        const response = await faturasConsultor(consultoresSelecionados, mesInicio, anoInicio, mesFim, anoFim);

        const values = _.groupBy(response.data, (value) => {
            return value.mes;
        });

        const result = _.map(values, (value, key) => {
            return [
                key,
                _.sumBy(values[key], (v) => v.receita)
            ]
        });
        const dadosGrafico = () => {
            return [["Consultor", "Receita"], ...result]
        };

        setGrafico(dadosGrafico);
        setShowRelatorio(false);
        setShowPizza(false)
        setShowGrafico(true)
    };

    const handPizza = async() => {
        const response = await faturasConsultor(consultoresSelecionados, mesInicio, anoInicio, mesFim, anoFim);
       
        const values = _.groupBy(response.data, (value) => {
            return value.no_usuario;
        });

        const result = _.map(values, (value, key) => {
            return [
                key,
                _.sumBy(values[key], (v) => v.receita)
            ]
        });
        const dadosGraficoPizza = () => {
            return [["Consultor", "Receita"], ...result]
        };
        setPizza(dadosGraficoPizza);
        setShowRelatorio(false);
        setShowPizza(true)
        setShowGrafico(false)
    }
    
    

    faturasAgrupadas.forEach((faturas, index) => {
        relatorioConsultores.push(
            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                    <TableHead>
                        <div><b>{index}</b></div>
                    </TableHead>
                    <TableHead>
                        <TableRow>
                            <TableCell> <b>Perodo</b> </TableCell>
                            <TableCell > <b>Receita Lquida</b> </TableCell>
                            <TableCell > <b>Custo Fixo</b> </TableCell>
                            <TableCell > <b>Comisso</b> </TableCell>
                            <TableCell > <b>Lucro</b> </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {faturas.map((row) => (
                            <TableRow
                                key={row.co_usuario}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell component="th" scope="row">
                                    {row.mes}
                                </TableCell>
                                <TableCell >{row.receita}</TableCell>
                                <TableCell >{row.receita}</TableCell>
                                <TableCell >{row.comissa}</TableCell>
                                <TableCell >{row.receita}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        )
    });

    relatorioGraficoBarras.push(
        <Chart
            chartType="ComboChart"
            width="100%"
            height="400px"
            data={grafico}
            options={options}
        />
    );

    relatorioPizza.push(
        <Chart
            chartType="PieChart"
            data={pizza}
            options={optionsPizza}
            width={"100%"}
            height={"400px"}
        />
    );


    return (
        <Box sx={{ width: '100%' }}>
            <Grid container rowSpacing={1} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
                <Grid item xs={5}>
                    <Item>
                        <div>Selecione o periodo</div>
                        <MonthPickerDropdown
                            CTabIndex={1}
                            label="Select Months"
                            searchTextLabel="Filter the months by typing here"
                            startYear={2007}
                            startMonth={1}
                            endYear={2007}
                            endMonth={12}
                            displayShortMonthName={true}
                            displayShortYearName={false}
                            hideCheckBox={true}
                            displayOkAndCancelButton={false}
                            onChange={handleAMonthChange}
                        />
                    </Item>
                </Grid>

                <Grid item xs={5}>
                    <Item>
                        <div>Selecione o Consultor</div>
                        <Select isMulti onChange={(item) => handleSelect(item)} options={consultores.map((consultor) => ({ value: consultor.co_usuario, label: consultor.no_usuario }))} />
                    </Item>

                </Grid>
                <Grid item xs={2}>
                    <Item>

                        <ButtonGroup   orientation="vertical" variant="contained" aria-label="outlined primary button group">
                            <Button onClick={handRelatorio}>Relatorio</Button>
                            <Button  onClick={handGrafico} >Grafico</Button>
                            <Button  onClick={handPizza}>Piza</Button>
                        </ButtonGroup>
                    </Item>
                </Grid>
                <Grid item xs={12}>
                    <div>{showRelatorio && relatorioConsultores}</div>
                    <div>{showGrafico && relatorioGraficoBarras}</div>
                    <div>{showPizza && relatorioPizza}</div>
                </Grid>
            </Grid>
        </Box>
    );
}
